import React, { Component, Fragment } from 'react';
import { Alert, Table, Badge } from 'reactstrap';
import { JOFGENGenerator } from '../../lib';
import Icon from '@mdi/react';
import { mdiAlertRhombus, mdiCheckCircle, mdiCloseCircle, mdiCircleHalf, mdiAlertCircle } from '@mdi/js';
import { local_urls } from '../../urls';
import { isValidValueJSON, isValidDesignJSON } from '../../lib';

export default class ValidationMethods extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    render() {
        return (
            <Fragment>
                <section id="methods">
                    <h2>
                        Validation methods
                    </h2>
                    <p>
                        These methods are intended for validation of objects to get init validation state of generated form or designer,
                        while designer and generator <b>DO NOT</b> validate objects during initialization.
                    </p>

                    <section className='card card-body mt-2 mb-2'>
                        <h4>
                            isValidValueJSON(json, value, mode)
                        </h4>
                        <p>
                            To check if <prm>value</prm> is valid for specific <prm>json</prm> object (form).
                            <div className="mt-2">
                                <table className="w-100 params text-dark">
                                    <tbody>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Parameters:</b>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>json</prm>
                                            </td>
                                            <td>
                                                <objpt>array of objects</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>value</prm>
                                            </td>
                                            <td>
                                                <objpt>object</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>mode</prm>
                                            </td>
                                            <td className="text-muted">
                                                <string>'linear'</string>|<string>'tree'</string>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Returns:</b> <span className="text-muted"><string>true</string> | <string>false</string></span>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>true</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>value</prm> is valid
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>false</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>value</prm> is not valid
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </p>
                    </section>

                    <section className='card card-body mt-2 mb-2'>
                        <h4>
                            isValidValueJSON(json, value, mode, sep)
                        </h4>
                        <p>
                            To check if <prm>value</prm> is valid for specific <prm>json</prm> object (form).
                            <div className="mt-2">
                                <table className="w-100 params text-dark">
                                    <tbody>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Parameters:</b>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>json</prm>
                                            </td>
                                            <td>
                                                <objpt>array of objects</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>value</prm>
                                            </td>
                                            <td>
                                                <objpt>object</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>mode</prm>
                                            </td>
                                            <td>
                                                <string>'linear_merge'</string>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>

                                            <td>

                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>sep</prm>
                                            </td>
                                            <td>
                                                <objpt>string</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                            <td>

                                            </td>
                                        </tr>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Returns:</b> <span className="text-muted"><string>true</string> | <string>false</string></span>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>true</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>value</prm> is valid
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>false</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>value</prm> is not valid
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </p>
                    </section>


                    <section className='card card-body mt-2 mb-2'>
                        <h4 id="parameter-value">
                            isValidDesignJSON(json, mode)
                        </h4>
                        <p>
                            To check if <prm>json</prm> object is valid.
                            <div className="mt-2">
                                <table className="w-100 params text-dark">
                                    <tbody>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Parameters:</b>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>json</prm>
                                            </td>
                                            <td>
                                                <objpt>array of objects</objpt>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <prm>mode</prm>
                                            </td>
                                            <td>
                                                <string>'linear_merge'</string>
                                            </td>
                                            <td>
                                                <Badge color="dark">mandatory</Badge>
                                            </td>

                                            <td>

                                            </td>
                                        </tr>
                                        <tr>
                                            <td colSpan="3">
                                                <b>Returns:</b> <span className="text-muted"><string>true</string> | <string>false</string></span>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>true</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>json</prm> is valid
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>
                                                <string>false</string>
                                            </td>
                                            <td>
                                            </td>
                                            <td>
                                                <prm>json</prm> is not valid
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </p>
                    </section>
                </section>
            </Fragment>
        );

    }
}